<template>
  <div class="node-wrap">
    <NodeWrapBox :node="node" @delNode="delNode" />
    <AddNodeBtnBox :node="node" @addnode="addnode" />
  </div>
</template>
<script>
import NodeWrapBox from "../Generator/node-wrap-box.vue";
import AddNodeBtnBox from "../Generator/add-node-btn-box";
export default {
  name: "NodeWrap",
  components: {
    NodeWrapBox,
    AddNodeBtnBox,
  },
  props: {
    node: {
      type: Object,
      default: undefined,
    },
  },
  methods: {
    addnode(node) {
      // console.log('node-wrap 新节点:')
      // console.log(node)
      this.$emit("addnode", node);
    },
    delNode() {
      this.$emit("delNode");
    },
  },
};
</script>
